import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigRetencaoFonte } from '../../../../models/dtos/mlf/configRetencaoFonte';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const ConfigRetencaoFonteSearchView: FC<SearchViewProps<ConfigRetencaoFonte>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'codigoReceita',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código da Receita',
    },
    {
      dataField: 'periodoApuracao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Período de Apuração',
    },
    {
      dataField: 'aliquotaTotal',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Alíquota',
    },
    {
      dataField: 'recolhimentoMinimo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Recolhimento Mínimo',
    },
  ];
  return (
    <WindowComponent title="Configurações da Retenção na Fonte">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigRetencaoFonteEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigRetencaoFonteSearchView;
